.box{
    background-color: white;
    border: none;
    border-radius: 10%;
    box-shadow: 0 0 8px #888;
    width: 5rem;
    height: 5rem;
    text-align: center;
    font-size: 5em;
    font-family: 'Fredoka One', cursive;
    font-weight: bold;
    line-height: 5rem;
    margin: 0.5rem;
    cursor: pointer;
}

.x{
    color: rgb(255, 70, 37);
}

.o{
    color: rgb(44, 135, 255);
}

.box:hover{
    box-shadow: 0 0 15px #888;
}